import React from 'react';
import ReactDOM from 'react-dom';
import './index.scss';
import App from './App';
import * as serviceWorker from './serviceWorker';
import { createStore } from 'redux';
import rootReducer from './rootReducer';
import { Provider } from 'react-redux';
const store = createStore(rootReducer);

ReactDOM.render( <Provider store={store}><App /></Provider>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();


document.addEventListener('DOMContentLoaded', () => {

	const serverHost = window.location.host
	console.log( serverHost);


	/// Test browser capabilities to determine version of pdfjs to use - es5 or 6. (mobile or desktop)

	const pdfVersion = (
		(typeof window['PDFJSDev'] === "undefined" || window['PDFJSDev'].test("SKIP_BABEL")) &&
		(typeof ReadableStream === "undefined" ||
			typeof Promise.allSettled === "undefined")
	) ? '2.3.200-es5' : '2.4.456'

	const script = document.createElement('script');
	script.type='text/javascript'
	script.src = `/www/pdf-${pdfVersion}/build/pdf.min.js`
	document.querySelector('head').appendChild(script);

	const script2 = document.createElement('script');
	script2.type='text/javascript'
	script2.src=`/www/pdf-${pdfVersion}/build/pdf.worker.min.js`
	document.querySelector('head').appendChild(script2);

})
