/// Sat 11 Apr 2020 10:56:32 GMT
/// pdf.js          var pdfjsVersion = '2.5.78';
/// pdf.worker.js   const pdfjsVersion = '2.5.78'
///



export const pdfThumbnailer = (url, canvasid) => {

	// If absolute URL from the remote server is provided, configure the CORS header on that server.
	//const url = 'https://assets.ctfassets.net/ji9dy7eny2j0/3hzsinotXLwhgku4mdvTcw/dfbd844457cbaf0210da7d8d11016f69/Electronics.pdf?w=50&h=50';

	/// Loaded via <script> tag, create shortcut to access PDF.js exports.
	const pdfjsLib = window['pdfjs-dist/build/pdf'];
	///const pdfjsLib = "localhost:3000/www/pdf/build/pdf.js";

	/// The workerSrc property shall be specified. - handled in script src in index.html.
	//pdfjsLib.GlobalWorkerOptions.workerSrc = null;//'./pdf.worker.js';


	// Asynchronous download of PDF
	const loadingTask = pdfjsLib.getDocument(url);
	loadingTask.promise.then(function(pdf) {
	  //console.log('PDF loaded');

	  // Fetch the first page
	  const pageNumber = 1;
	  pdf.getPage(pageNumber).then(function(page) {
	    //console.log('Page loaded');

	    const scale = 1;
	    const viewport = page.getViewport({
	      scale: scale
	    });

	    // Prepare canvas using PDF page dimensions
	    //const canvas = document.getElementById('the-canvas');
	    const canvas = document.getElementById(canvasid);
	    const context = canvas.getContext('2d');
	    canvas.height = viewport.height;
	    canvas.width = viewport.width;

	    // Render PDF page into canvas context
	    const renderContext = {
	      canvasContext: context,
	      viewport: viewport
	    };
	    const renderTask = page.render(renderContext);
	    renderTask.promise.then(function() {
	      //console.log('Page rendered');
	    });
	  });
	}, function(reason) {
	  // PDF loading error
	  console.error(reason);
	});

}
