
export const myFetcher = async(URL = '/itemsData.json', dispatch, delay = null) =>  {

	/// playing around with delay only in dev.
	const fetcher = async() => {
    try{
	    const res = await fetch(URL, {})
	    const resjson = await res.json();
		  dispatch({ type: 'APP_GOT_DATA', payload: resjson } );
		} catch (error){
			dispatch({ type: 'APP_GOT_DATA', payload: 'Error' } );
		}
	}


	if(delay){
		setTimeout( ()=>{ fetcher() }, delay)
	} else {
		fetcher();
	}
}

