import React from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { currentAssets } from './contentful.js';
import { pdfThumbnailer } from './reactpdf.js';
import movie from './movie.jpg'
import pdfimg from './icons8-pdf-64.png'

///		<!-- >DEV ONLY !!! -->
import {assets} from './assets-offline.js'


const AssetsComponent2 = () => {

	const dispatch = useDispatch();
	const currentApp = useSelector(state => state.app.currentApp);
	const hasData = useSelector(state => state.app.appsList[currentApp].hasData);
	const online = (!navigator.onLine) ? false : true;

	const chooserClick = (e) => {
		/// Nullify the chooser before open so that 'onChange' will fire even if no change...
		e.target.value = null;
	}

	const fileSelected = (e) => {
		if( ! e.target.files[0] ){
			console.log('file selection cancelled');
			dispatch({type: 'CLOSE_SEARCHBAR' });
			return
		}
		dispatch({type: 'OPEN_SEARCHBAR' });
		document.querySelector('.upload > div > div:nth-child(1)').innerHTML = e.target.files[0].name;

		// now user has chance to confirm or cancel upload...
		// handled in SearchbarComponent.
	}



	React.useEffect(() => {
		/// DEV ONLY - when developing you dont want to be online, so you have some sample data to hand.
		if( ! online ){
			dispatch({ type: 'APP_GOT_DATA', payload: assets } );
		}


		/// Make the Display 100% wide rather than .container class' default 90%
		document.querySelector('#Display').classList.add('DisplayContainer')

		/// When a functional component *un-mounts* the logic inside the return function will get executed.
		/// So remember to clean up your code if necessary by returning a function inside the useEffect function.
		return () => document.querySelector('#Display').classList.remove('DisplayContainer')

  }, [dispatch, online]);



	 ///// NOT DEV ONLY
	//if( ! online ){
		//return ( <div id='assetsEmpty' className=''>No internet connection.</div>)
	//}




	if( online && ! hasData ){

		/// Fetch data and dispatch response to store.
		(async () => {
				const response = await currentAssets();
				dispatch({ type: 'APP_GOT_DATA', payload: response } );
		})()

		/// While fetching...
		return (<div id='assetsEmpty' >Loading Assets...</div>)
	}

	if( /**?*/! online && ! hasData ){
		return (<div id='assetsEmpty' >Loading Assets...</div>)
	}

	if( hasData && hasData === 'Error' ){
		return (<div id='assetsEmpty' >Error loading content.</div>)
	}

	if( hasData ){
		//console.log(hasData)


		/// TODO: on uploads, look at the response, and figure out what the error is... (it does upload ok.)
		/// if(item.fields.file.contentType.indexOf  <<< cant get indexOf undefined... on the response of an upload

		const retrievedArray = hasData.items;
		const sortedItems = retrievedArray.sort((a, b) => Date.parse(new Date(b.sys.createdAt)) - Date.parse(new Date(a.sys.createdAt)))
		//{
			//return Date.parse(new Date(b.sys.createdAt)) - Date.parse(new Date(a.sys.createdAt))
		//})

		const itemsList = sortedItems.map((item, i) => {

			const juarel = 'https:' + item.fields.file.url

			/// Video
			if(item.fields.file.contentType.indexOf('video') > -1){
				return (
						<div key={i}  href={juarel}  className="assetBox"   style={{display:"inline-flex", alignItems:"center", backgroundImage:`url(${movie})`}}>
							<video className='video'   src={juarel}		style={{width:"100%"}}   preload="metadata"  controls  ></video>
						</div>
					)
			}
			/// PDF
			if(item.fields.file.contentType === 'application/pdf') {

				const pdfClickHandler = () => {
					// TODO: next step: open it in local pdfviewer.
					// set 'SearchTerm' of PDFComponent in reducer
					//dispatch ({ type:'PDF_URL', payload: juarel })
					//dispatch ({ type:'CHANGE_APP_STATE', payload: 'PDFComponent' })

					const pdfVersion = (
						(typeof window['PDFJSDev'] === "undefined" || window['PDFJSDev'].test("SKIP_BABEL")) &&
						(typeof ReadableStream === "undefined" ||
							typeof Promise.allSettled === "undefined")
					) ? '2.3.200-es5' : '2.4.456'
					console.log(pdfVersion);
					const serverOrigin = window.location.origin
					window.open(`${serverOrigin}/www/pdf-${pdfVersion}/web/viewer.html?file=${juarel}`, '_blank');
				}

				const canvasid=`the-canvas${i}`
				pdfThumbnailer(juarel, canvasid) // puts retrieved pdf thumbnail into the canvas.

				return (
						<div  key={i} className="assetBox" style={{backgroundImage:`url(${juarel})`, position:"relative"}}>

							<canvas id={canvasid} style={{border: "1px solid grey", direction: "ltr",  width: "100%", overflow: "hidden",  height:"100%", position:"absolute", left:"0", textAlign:'center'}}>
							</canvas>

							<div style={{position:"absolute", display:"inline-block", width:"100%", height:"100%", zIndex:"2", backgroundImage:`url(${pdfimg})`, backgroundSize:"50%", backgroundRepeat:'no-repeat', backgroundPosition:'center'}} onClick={pdfClickHandler} >
							</div>

						</div>
					)
			}
			/// Images (default)
			return (
					<a  key={i} href={juarel}  target="_blank" rel="noopener noreferrer" className="assetBox" style={{backgroundImage:`url(${juarel}?w=150&h=150)`}}> </a>
				)

		})


		/// ... and display them in the main area.
		return (
			<div id='assetsFull' className='container-center'>
				<input type="file" style={{display:'none'}} onChange={fileSelected} onClick={chooserClick}/>
				{ itemsList }
			</div>
			)
	}

}
export default AssetsComponent2



