import React from 'react';
import { useSelector } from 'react-redux';
import ChilliHotComponent from './ChilliHotComponent';
import HackerNewsComponent from './HackerNewsComponent';
import RedditComponent from './RedditComponent';
import RestDBCommentsComponent from './RestDBCommentsComponent';
import CommentsComponent from './Contentful/CommentsComponent';
import AssetsComponent from './Contentful/AssetsComponent';
//import PDFComponent from './Contentful/PDFComponent'; <<< unused - going directly to pdfviewer - see TODO in PDFComponent.js

const Display = () => {

	const currentApp = useSelector(state => state.app.currentApp);

	let content = '';
	switch(currentApp){
		case 'reddit':
			content = <RedditComponent />
			break
		case 'Hacker News':
			content = <HackerNewsComponent />
			break
		/// Default currentApp on load
		case 'Chilli Hot':
			content = <ChilliHotComponent />
			break
		case 'Tester':
			/// Click action on buttons for fonts sizes
			const handlePlus = (e) => {
				console.log(e.target.innerHTML, window.innerWidth, window.innerHeight);
				const el = e.target.innerHTML

				const w = window.innerWidth
				const h = window.innerHeight
				let f = parseInt(window.getComputedStyle(document.querySelector('.brand-logo')).fontSize);
				let i = parseInt(window.getComputedStyle(document.querySelector('nav i')).fontSize);
				f = parseInt(f)
				f = (el === 'increase font size') ? parseInt(f + 2) : parseInt(f - 2)
				i = (el === 'increase font size') ? parseInt(i + 1) : parseInt(i - 1)
				document.querySelector('.brand-logo').style.fontSize = f + 'px'
				document.querySelector('nav i').style.fontSize = i + 'px'

				const pdfVersion = (
					(typeof window['PDFJSDev'] === "undefined" || window['PDFJSDev'].test("SKIP_BABEL")) &&
					(typeof ReadableStream === "undefined" ||
						typeof Promise.allSettled === "undefined")
				) ? '2.3.200-es5' : '2.4.456'

				document.querySelector('.readout').innerHTML = `Screen Width(px): ${w}<br /> Screen Height(px): ${h}<br /> Logo font(px): ${f}<br /> Icon font(px): ${i}<br />This browser will use pdfVersion: ${pdfVersion}`;
			}
			return (
				<div id='Display' className='container'>
					<div id='tester'>
						<button  onClick={handlePlus}>increase font size</button><br />
						<button  onClick={handlePlus}>decrease font size</button><br />
						<div className='readout'></div>

					</div>
				</div>
			)
		case 'Comments':
			content = <CommentsComponent />
			break;
		case 'RestDBComments':
			content = <RestDBCommentsComponent />
			break;
		case 'Assets':
			content = <AssetsComponent />
			break;
		case 'PDFComponent':
			//content = <PDFComponent />

			const fallbackUrl = 'https://assets.ctfassets.net/ji9dy7eny2j0/3hzsinotXLwhgku4mdvTcw/dfbd844457cbaf0210da7d8d11016f69/Electronics.pdf'
			window.open("http://localhost:3000/www/pdf/web/viewer.html?file=" + fallbackUrl, '_blank');
			content = <ChilliHotComponent />
			return  (<div id='Display' className='container'> { content }  </div>)

		default:

	}

	return (<div id='Display' className='container'> { content }  </div>)
}

export default Display


